export default {
	/**
	 * 数字截取
	 * @param {Object} val 值域
	 * @param {Object} div 截取位数
	 */
	number: (val, div,bool) => {
		if(!bool){
			return Number(val).toFixed(div);
		}else{
			if(val && val.length > 0){
				let values = [];
				let split = val.split(".");
				if(split.length > 0){
					values.push(split[0]?split[0]:0);
					if(!split[1]){
						for(let i = 0; i < div; i++){
							values.push(0);
						}
					}else{
						let decimals = split[1].split("");
						if(decimals.length > 0){
							for(let i = 0; i < div; i++){
								if(!decimals[i]){
									decimals[i] = 0;
								}
								values.push(decimals[i]);
							}
						}
					}
				}
				return values.join(".");
			}
			return Number(val).toFixed(div);
		}
	},
	/**
	 * 分位数
	 * @param {Object} val
	 * @param {Object} num 分位标准3位,默认分隔符号","
	 */
	quantile: (val, num) => {
		if (!val) return val;
		if (val) {
			let integer = val.toString().split(".")[0];
			let decimal = val.toString().split(".")[1];
			if (!integer) return val;
			let result = [];
			if (integer) {
				let n = integer.toString().split("");
				let count = 0;
				for (let i = n.length - 1; i >= 0; i--) {
					count++;
					result.unshift(n[i]);
					if (!(count % num) && i != 0) {
						result.unshift(',');
					}
				}
			}
			return result.join("") + (decimal.length > 0 ? ('.' + decimal.toString()) : '');
		}
	},
	/**
	 * 时间格式化
	 * @param {Object} date 当前时间值（一般为时间戳）
	 * @param {Object} fmt 时间自定义格式,ey:'yyyy-MM-dd hh:mm:ss'
	 */
	format: (date, fmt) => {
		if (typeof date === "number") {
			date = new Date(date);
		}else{
			if(date.indexOf("-") > -1){
				date = date.replace(/-/g,'/').replace(/T/g,' ').replace(/\+/g,'');
			}
			date = date? new Date(date): new Date();
		}
		if (/(y+)/.test(fmt)) {
			fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
		}
		let o = {
			'M+': date.getMonth() + 1,
			'd+': date.getDate(),
			'h+': date.getHours(),
			'm+': date.getMinutes(),
			's+': date.getSeconds()
		}
		for (let k in o) {
			if (new RegExp(`(${k})`).test(fmt)) {
				let str = o[k] + ''
				fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : ('00' + str).substr(str.length))
			}
		}
		return fmt;
	},
	/**
	 * 根据要求对数据作取万相关处理
	 * @param {Object} val 当前数据
	 * @param {Object} dig 位数调整
	 */
	digit: (val,dig) => {
		if(!val) return val;
		if(val){
			let nums = val.toString().split(".");
			let iteger = nums[0].split(""),decimal = nums[1];
			//对整数位数取值;
			if(dig > iteger.length){
				let div = dig - iteger.length;
				while(div > 0){
					div --;
					iteger.unshift(0)
				}
			}
			
			iteger.splice((iteger.length - dig),0,'.');
			return iteger.join("")+(decimal?decimal+'':'');
		}
	},
}
