import Constant from './constant.js';

const formatNumber = n => {
  n = n.toString()
  return n[1] ? n : '0' + n
}

export function formatTime(value, type) {
  value = Number(value)
  var date = new Date(value)// 一定要记得写这个，不然会报date.getFullYear is not a function
  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()
  const hour = date.getHours()
  const minute = date.getMinutes()
  const second = date.getSeconds()

  if (type === 1) {
    return [hour, minute].map(formatNumber).join(':')
  } else if (type === 2) {
    return [year, month, day].map(formatNumber).join('/')
  }  else if (type === 3) {
    return [month, day].map(formatNumber).join('/')
  } else {
    return [year, month, day].map(formatNumber).join('/') + ' ' + [hour, minute, second].map(formatNumber).join(':')
  }
}

export function geTel(tel) {
  var reg = /^(\d{3})\d{4}(\d{4})$/
  if (tel) {
    console.log(1)
    return tel.replace(reg, '$1****$2')
  } else {
    return ''
  }
}

export function formatScore(n) {
  const n1 = Number(n).toFixed(2)
  return n1.substring(n1.length - 1) === '0' ? n1.substring(0, 3) : n1.substring(0, 4)
}

