// 获得当前年月
export function getCurDateYM() {
  const date = new Date()
  const year = date.getFullYear()
  let month = date.getMonth() + 1
  month = month > 9 ? month : '0' + month
  return `${year}-${month}`
}

// 格式化时间 Y-M-D HH:MM:ss
export function formatTimeYMDHM(number) {
  number = typeof number === 'number' ? number : Number(number)
  const now = new Date(number)
  const year = now.getFullYear()

  let month = now.getMonth() + 1
  let date = now.getDate()
  let hour = now.getHours()
  let minute = now.getMinutes()
  let seconds = now.getSeconds()
  month = month > 9 ? month : '0' + month
  date = date > 9 ? date : '0' + date
  hour = hour > 9 ? hour : '0' + hour
  minute = minute > 9 ? minute : '0' + minute
  seconds = seconds > 9 ? seconds : '0' + seconds
  return year + '-' + month + '-' + date + ' ' + hour + ':' + minute + ':' + seconds
}
// 格式化时间 Y-M-D HH:MM:ss
export function formatTimeString(number) {
  number = typeof number === 'number' ? number : Number(number)
  const now = new Date(number)
  const year = now.getFullYear()

  const month = now.getMonth() + 1
  const date = now.getDate()
  const hour = now.getHours()
  const minute = now.getMinutes()
  const seconds = now.getSeconds()
  return year + '年' + month + '月' + date + '日' + hour + '时'
}
// 格式price/useScore
export function disposalPrice(list) {
  return list.map((item) => {
    item['price'] = item['price'] ? item['price'] / 100 : ''
    item['useScore'] = item['useScore'] ? item['useScore'] / 100 : ''
    return item
  })
}

export 	function countBrowseNum(num) {
  if (!num) return num
  if (num > 99999) return parseInt(num / 10000) + 'w+'
  return num / 10000 < 1 ? num : Math.floor((num / 10000) * 10) / 10 + 'w+'
}

/**
 * 用户title提示
 * @param {Object} type
 */
export function getTitleByType(type){
	switch(type){
		case 0: return "浏览记录";
		case 1: return "点赞记录";
		case 2: return "收藏记录";
		default: return "其它"
	}
}

/**
 * 设置图片居中，不拉伸
 */
export function imageStrenth(src,defaultImg = Constant.defaultImg){
	if(src){
		return src.indexOf("?") > -1?src:src+'?imageView2/1/w/750/h/498/interlace/1';
	}
	return defaultImg;
}

